body, html {
	margin: 0;
	padding: 0;
	background:#20615b  !important;
	min-height: 100%;
	text-align: center;
    }
			
.user_card {
	width: 90vw;
	background: #dece9c;
	position: relative;
	display: flex;
	justify-content: center;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	-webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	-moz-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	border-radius: 5px;
	bottom: 110px;
	height: 100px
}
.brand_logo_container {
	position: relative;
	height: 160px;
	width: 170px;
	border-radius: 40%;
	background:#20615b;
	padding: 10px;
	text-align: center;
	bottom: 80px
}
.brand_logo {
	height: 140px;
	width: 150px;
	border-radius: 40%;
	border: 2px solid white;
}

.container {
	padding-top: 30px;
}
@media all and (max-width: 420px) {
	h1, h2 { font-size: 1.8rem; }
	}
@media all and (max-width: 420px) {
	.heading-container { top: 10px !important; }
	}
@media all and (max-width: 420px) {
	.user_card { height: 85px !important; }
	}
@media all and (max-width: 370px) {
	h1, h2 { display: none; }
	}
@media all and (max-width: 420px) {
	.container { padding-top: 60px; }
	}
@media all and (max-width: 370px) {
	.container { padding-top: 190px; }
	}